// Colors
// ---------------------------------

// Default

$color-default-black:               #000000;
$color-default-white:               #ffffff;
$color-transparent:                 rgba(255, 255, 255, 0);
$color-transparent-05:              rgba(255, 255, 255, 0.5);

// Project palette
// для нейминга цветов используем https://www.htmlcsscolor.com/hex/334482

$color-charcoal:                    #424242;
$color-amulet:                      #8b9d83;
$color-xanadu:                      #6d8e5e;
$color-cerulean-blue:               #316dc2;
$color-desert-storm:                #ede8e2;
$color-wild-watermelon:             #ff5e66;
$color-mischka:                     #a9afb7;
$color-matterhorn:                  #515151;
$color-light-grey:                  #d4d4d4;
$color-wan-white:                   #e6e3db;
$color-storm-dust:                  #686656;

// Overlay

$overlay-blue:                       rgba($color-cerulean-blue, 0.2);
$overlay-dark :                    rgba(13, 29, 51, 0.4);

// Gradient

$black-to-right:                    linear-gradient(90deg, rgba(0, 0, 0, 0.75) 0%, rgba(0, 0, 0, 0) 75%);

// Typography
// ---------------------------------

// переменная используемая в html для подключения скейлинга
$fz-default:                        18px;

$ff-roboto:                        "Roboto", "Arial", sans-serif;

// Animation
// ---------------------------------

$tf-default:                        ease;
$trans-default:                     0.3s $tf-default;

// если требуется изинг отличный от $tf-default, то переменную называем не цифрой
$trans-modal:                       0.6s cubic-bezier(0.55, 0, 0.1, 1);

$trans-600:                         0.6s $tf-default;

// Viewports
// ---------------------------------

$vp-320:                            320px;
$vp-345:                            345px;
$vp-375:                            375px;
$vp-430:                            430px;
$vp-768:                            768px;
$vp-860:                            860px;
$vp-930:                            930px;
$vp-1024:                           1024px;
$vp-1200:                           1200px;
$vp-1280:                           1280px;
$vp-1440:                           1440px;
$vp-1660:                           1660px;
$vp-1920:                           1920px;

// Retina
// ---------------------------------

$retina-dpi:                        144dpi;
$retina-dppx:                       1.5dppx;

$width-background: calc(100vw - 5%);
$width-background-small: calc(100vw - 10%);
